* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__dropdown{
  background-color: #fff;
}

.app__header > h1{
  color: #fc3c3c;
  font-size: 2rem;
}

.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__left {
  flex: 0.9;
}
.app__stats {
  display: flex;
  justify-content: space-between;
}
/* dashboard color: #473F97 purple */
/* lighter purple: #6C65AC */

.app__right {
  padding: 10px;
}
@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
  .app__right {
    margin: 30px 0 0 15px;
  }
}
